import './Identity-Provider-Integration.scss';
import React, { useState, useEffect } from 'react';
import FlowTemplate from '../../Components/idpintegrationflows/idpFlowTemplate/flowTemplate.js'
import DisabledPage from "../../Components/disabledPage/disabled-page";
import NonAdminUser from '../../Components/nonAdminUser/non-admin-user';
import {isAdminUser}  from '../../Components/utils/general-utils';
import {useFlag} from "@unleash/proxy-client-react";
import TwoFactorCheck from "../../Components/twoFactorCheck/two-factor-check";


const IdentityProviderIntegration = (props) => {
    var userToken = null;
    const [currentUser, updateUser] = useState({
        adminUser: true,
        selfServiceIDP: <FlowTemplate />
    })
    var isSelfServiceIDPEnabled = useFlag('iamconsole.IDPIntegration.enabled');

    useEffect(() => {
        insights?.chrome?.appAction?.('identity-provider-integration');
        insights.chrome.auth.getToken().then(token => {
            userToken = token;
            // if the user is in fact and admin user then render TwoFactorCheck
            if(isAdminUser(userToken) && isSelfServiceIDPEnabled){
                updateUser({
                    adminUser: true,
                    selfServiceIDP: <FlowTemplate />
                })
            }

            // check to see if 2fa feature flag is set. if not then render the proper error page
            if (!isSelfServiceIDPEnabled) {
                updateUser({
                    adminUser: false,
                    selfServiceIDP: <DisabledPage/>
                })
            }

        })
    }, []);



    return (
        <React.Fragment>
            {currentUser.selfServiceIDP}
        </React.Fragment>
    )

}

export default IdentityProviderIntegration
