// step one of the flow https://miro.com/app/board/uXjVO_x__QE=/

import React, {useState} from 'react';
import { 
    Text,
    TextInput,
    Radio,
    Title,
    Form,
    FormGroup
} from '@patternfly/react-core';
import {processAllInputs, setStateValue, verifyInput} from '../../utils/general-utils.js';

const CustomerInformation = (props) => {

    const [validations, setValidations] = useState({
        'custName': {
            inputType: 'GENERAL',
            status: 'default',   // success, warning, error, or default
            message: ''
        },
        'protocol': {
            inputType: 'RADIO', // just need to know that it is checked so the value here doesnt matter.
            status: 'default',
            message: ''
        }
    });


    /**
     * handle selection needs to update information being used to govern
     * the applicaton flow as well as the final api update object
     */
    const handleSelection = (value) => {
        // regardless of current state. if the user alters the input clear existing flags. This should also reset
        // the currently displayed errors
        props.resetNextClicked(false);

        //  update the main data storage object with the new value
        props.rootUserChoices.current[value.target.id] = value.target.value;

        // update the validation settings. It is expected that the id's used in the validation object matches
        // the id's used on the elements.
        const failureTriggered = processAllInputs( props.rootUserChoices.current ,validations , setValidations );

        props.rootValidation.current['CUSTINFO'] = !failureTriggered;

        if(!failureTriggered && props.rootUserChoices.current.protocol){
            props.updateStatus(props.rootUserChoices.current.protocol);
        }

    }

// pull from props.inputTrigger to handle change values
    return <Form>
                <Title headingLevel="h2">Customer Information</Title>
                <Text className="pf-u-color-400 pf-u-font-size-sm">All fields are required</Text>
                <FormGroup  label="Identity provider display name"
                            helperText="Enter a personalized display name for your Identity Provider (IdP). It is important to use unique names for your IdP display name, since multiple Red Hat accounts can be linked to one IdP."
                            helperTextInvalid={validations.custName.message}
                            validated={props.nextClicked ? validations.custName.status : 'default'}
                            onChange={(value) => {handleSelection(value)}}>
                    <TextInput
                               type="text" 
                               id="custName"  />
                </FormGroup>

                <FormGroup  onChange={(value) => {handleSelection(value)} }
                            role="radiogroup" 
                            fieldid="authType" 
                            label="Choose identity provider protocol"
                            helperTextInvalid={ "A protocol must be selected to proceed"}
                            validated={ props.nextClicked ? validations.protocol.status : 'default'}
                            id="protocol">
                    <Radio name="authType" id="protocol" label="OpenID Connect" value="OIDCCONFIG"/>
                    <Radio name="authType" id="protocol" label="Saml 2.0" value="SAMLCONFIG"/>
                </FormGroup>

            </Form>

}

export default CustomerInformation;
