import './two-factor-check.scss';
import {isAdminUser} from '../utils/general-utils';
import React, { useEffect, useState } from 'react';
import { 
    Title,
    Text,
    Button,
    FormGroup,
    Checkbox,
    ActionGroup,
    Popover,
    PageSection,
    Form 
} from '@patternfly/react-core';
import ExternalLinkSquareAltIcon from '@patternfly/react-icons/dist/esm/icons/external-link-alt-icon';
import QuestionIcon from '@patternfly/react-icons/dist/esm/icons/outlined-question-circle-icon'
import { useDispatch } from 'react-redux'
import { addNotification } from '@redhat-cloud-services/frontend-components-notifications/redux';

var docLink = "";
var statusURL = "http://localhost:8081/auth/realms/redhat-external/apis/organizations/v1/my/authentication-policy";
if(insights.chrome.getEnvironment() === 'stage'){
    statusURL = "https://sso.stage.redhat.com/auth/realms/redhat-external/apis/organizations/v1/my/authentication-policy";
    docLink = "https://access.stage.redhat.com/documentation/en-us/red_hat_customer_portal/1/html/using_two-factor_authentication/con-ciam-2fa-about_two-factor-authentication";
}
else if (insights.chrome.getEnvironment() === 'prod'){
    statusURL = "https://sso.redhat.com/auth/realms/redhat-external/apis/organizations/v1/my/authentication-policy";
    docLink = "https://access.redhat.com/documentation/en-us/red_hat_customer_portal/1/html/using_two-factor_authentication/con-ciam-2fa-about_two-factor-authentication"
}

const TwoFactorCheck = (props) => {
    const dispatch = useDispatch();

    // build the state hooks
    const [checkState,updateCheckState] = useState ({
        checked: false,
        changed: false,
    });
    const [loadingState, updateLoadingState] = useState ({loading: true});

    const pullCurrentState = () => {
        // Need to establish the existing state by querying the base endpoint. 

        fetch(statusURL,{
            method: 'GET',
            referrerPolicy: 'no-referrer',
            headers:{
              'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + props.tokenData
            }
        })
        .then(response => response.json())
        .then(data => {
            if(data.error){
                console.error(data.error);
                FailureAlert('Failed to fetch the current status');
            }
            else{
                /*
                data expected to resemble
                {
                    authenticationFactors: {
                        otp: {
                            required: false
                        }
                    }
                }
                */
                updateCheckState({
                    checked: data.authenticationFactors.otp.required,
                         changed: false
                });
                updateLoadingState({
                    loading: false
                })
            }
        });
    }

    // equivalent to componentDidMount when provided an empty array as a second param
    useEffect(() => {pullCurrentState()},[]);

    const toggleCheckBox = () => {
        updateCheckState({
            checked: !checkState.checked,
            changed: true
        })
    }

    const SuccessAlert = () => {
        dispatch(
          addNotification({
            variant: 'success',
            description: 'Authentication policy settings saved',
            title: "Success"
          })
        );
      };

    const FailureAlert = (er) => {
        dispatch(
            addNotification({
              variant: 'danger',
              description: er,
              title: 'Error'
            })
        );
    };
      

    // following the api: https://docs.google.com/document/d/1kj0ZTwFx2-vmUYogW6Iz7d4DzQ-U1r8DmiQV1qWTPTA/edit
    const updateStatus = (event) => {
        let payload = {"authenticationFactors" : {"otp": {"required": false}}};
        checkState.checked? payload.authenticationFactors.otp.required=true:payload.authenticationFactors.otp.required=false;
        insights.chrome.auth.getToken().then(token =>{
            fetch(statusURL,{
              method: 'POST',
              referrerPolicy: 'no-referrer',
              headers:{
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + token
              },
              body: JSON.stringify(payload)
          }).then(response => response.json())
            .then(data =>  {

                if(data.error){
                    console.error(data.error);
                    FailureAlert(data.error);
                }

                updateCheckState({
                    checked: data.authenticationFactors.otp.required,
                    changed: false
                });

                SuccessAlert();
            });
          });
    }
  
    return <PageSection>
                <Form>
                    <div>
                        <Title headingLevel="h2" size="xl">Organizational two-factor authentication</Title>
                            
                        <Text className="primaryTextBox pf-u-mt-md">
                            Enable organizational two-factor authentication to require all users to use a password and a 
                            one-time code to log in. When enabled, each user in your organization must install an authenticator application on 
                            a compatible mobile device in order to log in. 
                            <Button variant="link" href={docLink} target="_blank" component="a" isInline icon={<ExternalLinkSquareAltIcon />} iconPosition="right" className="pf-u-ml-xs">
                                Learn more about two-factor authentication
                            </Button>
                        </Text>
                            
                        <FormGroup className="pf-u-mt-md">
                            <Checkbox onChange={toggleCheckBox} label="Enable two-factor authentication for your organization" id="confirmed" className="twofaCheck" isChecked={checkState.checked}></Checkbox>
                            <Button id="infoIcon" variant="link"  component="span" isInline className="questionIcon pf-u-ml-xs" icon={<QuestionIcon/>}></Button>
                            <Popover
                            aria-label="Advanced popover usages example"
                            position={"right"}
                            hideOnOutsideClick={true}
                            enableFlip={true}
                            appendTo={() => document.body}
                            maxWidth={"18rem"}
                            minWidth={"2rem"}
                            bodyContent={
                                <div>
                                    Users will be required to set up two-factor authentication the next time they attempt to log in.
                                </div>
                            }
                            reference={() => document.getElementById("infoIcon")}
                            className="pf-u-text-align-center"
                            ></Popover>
                        </FormGroup>
                            
                        <ActionGroup className="pf-u-mt-3xl">
                            <Button onClick={updateStatus} isDisabled={loadingState.loading || !checkState.changed}>Save</Button>
                            <Button onClick={pullCurrentState} variant="link" isInline className="pf-u-ml-lg">Cancel</Button>
                        </ActionGroup>
                    </div>
                </Form>
            </PageSection>

    
}


export default TwoFactorCheck;
