import React from 'react';
import {
    Button
} from '@patternfly/react-core';

import {
    WizardFooterWrapper,
    useWizardContext,
} from '@patternfly/react-core/dist/esm/next/components/Wizard';


const SubmitFooter = (props) =>{
    const { goToNextStep, goToPrevStep } = useWizardContext();
    const [isLoading, setIsLoading] = React.useState(false);
    /**
     * Governs what happens when the next button gets clicked. Important for allowing the back and forth communication
     * between the footer element and the actual component being displayed when needing to perform steps such as validation.
     */
    const handleNextClick = async () => {
        console.log("Submitting with this data: " + JSON.stringify(props.userChoiceData, null, 2));

        setIsLoading(true);
        await new Promise(resolve => setTimeout(resolve, 2000));
        setIsLoading(false);
        goToNextStep();
    }

    return (
        <WizardFooterWrapper>
            <Button onClick={handleNextClick} isLoading={isLoading} >Next</Button>
            <Button variant="secondary" onClick={goToPrevStep}>
                Back
            </Button>
        </WizardFooterWrapper>
    );
}

export default SubmitFooter;
