import React from 'react';
import {
    Button
} from '@patternfly/react-core';

import {
    WizardFooterWrapper,
    useWizardContext,
} from '@patternfly/react-core/dist/esm/next/components/Wizard';


const GenericFooter = (props) =>{
    const { goToNextStep, goToPrevStep } = useWizardContext();
    /**
     * Governs what happens when the next button gets clicked. Important for allowing the back and forth communication
     * between the footer element and the actual component being displayed when needing to perform steps such as validation.
     */
    const handleNextClick = () => {
        if(props.valid){
            goToNextStep();
        }
        else{
            props.next(true);
        }
    }

    return (
        <WizardFooterWrapper>
            <Button onClick={handleNextClick} disabled={props.valid}>Next</Button>
            <Button variant="secondary" onClick={goToPrevStep}>
                Back
            </Button>
        </WizardFooterWrapper>
    );
}

export default GenericFooter;
