import './configuredIDP.scss'
import React, { useState } from 'react';
import {
    FormGroup,
    Button,
    Title,
    Card,
    Grid,
    Modal,
    ModalVariant,
    Icon,
    Flex,
    FlexItem
} from '@patternfly/react-core';

import IDPField from "./idpField";

import Exclamation from "@patternfly/react-icons/dist/esm/icons/exclamation-triangle-icon"

const ConfiguredIDP = (props) => {

    // flag to control which values get displayed
    const [oidcConfigType, setIsOIDCConfig] = useState(true);
    const [currentValues, setCurrentValues] = useState({
        displayName: "Test Company",
        clientID: "awesomesauce.com",
        clientSecret: "*******",
        logoUrl: "awesomesauce.com/logo"
    });
    const [displayConfirmationModal, setModalDisplay] = useState(false);

    /**
     * Triggered by the confirm button in the popup modal. This will submit the query to enable the third party idp
     */
    const onConfirm = () => {
        console.log("Enable IDP");
    }
    /**
     * Triggered by the cancel button in the modal, it will simply drop the modal
     */
    const onCancel = () => {
        setModalDisplay(false);
    }
    /**
     * Triggered by the Enable button on the root of this page. it will trigger the confirmation popup.
     */
    const onEnable = () => {
        setModalDisplay(true);
    }
    /**
     * Triggered by the delete button on the root page, this button will fire off the query to the api responsible for
     * removing the existing configuration.
     */
    const onDelete = () => {
        console.log("Delete this IDP config");
    }

    let lastAvailDate = "January 1st, 1979. 1:00 AM";

    /**
     * Custom header used for the modal popup to fulfill target design including icon
     */
    const customPopupHeader = (
        <React.Fragment>
            <Flex>
                <FlexItem>
                    <Icon status="warning"><Exclamation/></Icon>
                </FlexItem>
                <FlexItem>
                    <Title id="modal-custom-header-label" headingLevel="h1">
                        Enable Identity Provider for your entire organization?
                    </Title>
                </FlexItem>
            </Flex>

        </React.Fragment>
    )

    return (
                <Card className="configuredCard" isCompact={false}>
                    <Modal
                        variant={ModalVariant.small}
                        isOpen={displayConfirmationModal}
                        title="Enable Identity Provider for your entire organization?"
                        header={customPopupHeader}
                        aria-labelledby="modal-custom-header-label"
                        aria-describedby="modal-custom-header-description"
                        actions={[
                            <Button onClick={onConfirm}>Confirm</Button>,
                            <Button variant="link" onClick={onCancel}>Cancel</Button>
                        ]}
                    >
                        Are you sure you wish to enable Third Party SSO for your entire organization?
                    </Modal>
                    <Grid
                        hasGutter
                        span={12}>

                        {/* Display Name */}
                        <Grid hasGutter span={6}>
                            <IDPField idValue={"displayName"} title={"Display Name"} current={currentValues.displayName} />
                        </Grid>


                        <Grid hasGutter span={6}>
                            {/* Client Id  */}
                            <IDPField idValue={"clientID"} title={"Client ID"} current={currentValues.clientID} />

                            {/* Client Secret */}
                            <IDPField idValue={"clientSecret"} title={"Client Secret"} current={currentValues.clientSecret} />
                        </Grid>

                        {/* Logo URL */}
                        <Grid hasGutter span={6}>
                            <IDPField idValue={"logourl"} title={"Logo URL"} current={currentValues.logoUrl}/>
                        </Grid>

                    </Grid>

                    {/* Action buttons */}
                    <FormGroup className="buttonGroup">
                        <Button variant="primary" onClick={onEnable}>Enable</Button>
                        <Button variant="link" onClick={onDelete}>Delete</Button>
                    </FormGroup>

                </Card>

    )
}

export default ConfiguredIDP;
